/*A TELA MÉDIA MENOR É BASICAMENTE A TELA MÉDIA/GRANDE COM ALGUMAS ALTERAÇÕES NAS DIMENSÕES DA IMAGEM DO CELULAR E TAMBÉM DO ESPAÇAMENTO ENTRE PALAVRAS E ITENS*/

@media only screen and (min-width: 771px) and (max-width: 1000px){
  @font-face {
    font-family: Graphik-Regular;
    src: url('https://cdn.nubank.com.br/fonts/Graphik/variants/Graphik-Regular.woff2') format('woff2'),url('https://cdn.nubank.com.br/fonts/Graphik/variants/Graphik-Regular.woff') format('woff'),url('https://cdn.nubank.com.br/fonts/Graphik/variants/Graphik-Regular.ttf') format('truetype'),url('https://cdn.nubank.com.br/fonts/Graphik/variants/Graphik-Regular.svg#Graphik-Regular') format('svg'),url('https://cdn.nubank.com.br/fonts/Graphik/variants/Graphik-Regular.eot?#iefix') format('embedded-opentype');
    font-style: normal;
    font-display: fallback;
  }

  body {
    font-family: Graphik-Regular;
  }

  /*ESTILO BARRA DE NAVEGAÇÃO*/
  nav{
      background-color: white;
      box-shadow: 0 0 0.2em rgb(175, 175, 175); /* offset-x | offset-y | blur-radius | color */
      display: flex;
      height: 80px;
      align-items: center;
      justify-content: space-between;
      position: fixed;
      width: 100%;
      top: 0%;
  }

  /*bloco de menu*/
  .menu{
      width: 1000px;
      height: 38px;
      display: flex;
      overflow: hidden; /*O que ultrapassar é cortado e o restante do conteúdo fica invisível*/
  }

  .logo img{
      width: 45px;
      padding: 0px 20px 0px 32px; /*distância entre o conteúdo e a borda superior | direita | inferior | esquerda */
  }

  .logo a{
      text-decoration: none;
  }

  /*estilo da lista presente no menu da barra de navegação*/
  ul{
      width: 800px;
      height: 38px;
      list-style: none;
      display: flex;
      justify-content: space-between;
      margin-left: -15px;
  }

  li{
      max-height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      white-space: nowrap; /*recolhe os espaços em branco e suprime a quebra de linha*/
  }

  li a span{
      color: rgb(138, 5, 190);
      font-weight: bold;
  }

  li a{
      text-decoration: none;
      color: rgb(118, 118, 118);
      font-size: 17px;
      font-weight: bold;
      max-height: 38px;
      padding: 0px 0px 3px 30px;
  }

  li a:hover{
      text-decoration: none;
      color:rgb(186, 77, 227);
  }

  /*bloco para os botões de login*/
  .Login{
      display: flex;
      align-items: center;
      height: 100%;
      width: 650px;
  }

  /*botão de login*/
  .bt_login{
      height: 80px;
      width: 121px;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .bt_login a{
      color:rgb(138, 5, 190);
      text-decoration: none;
      font-size: 17px;
      font-weight: bold;
  }

  .bt_login a:hover{
      text-decoration: underline;
  }

  /*botão "quero ser nu"*/
  .bt_querosernu{
      background-color: rgb(138, 5, 190);
      display: flex;
      border-radius:6.25rem;
      padding-left:1.25rem;
      padding-right:1.25rem;
      height: 48px;
      align-items: center;
  }

  .bt_querosernu a{
      text-decoration: none;
      color: white;
      font-weight: bold;
      font-size: 17px;
  }

  /*BOTÃO DE MENU DE TELAS PEQUENAS - NÃO EXIBIDO*/
  .bt_elementos{
      display: none;
  }

  /*estilo do header*/
  header{
      height: 1013px;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  /*primeiro bloco*/
  .conteiner{
      background-image: url("../img/fundo1.jpg");
      background-size: cover;
      width: 89%;
      height: 74%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
  }

  /*bloco para o cabeçalho*/
  .cont-esquerdo{
      width: 50%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .cabecalho{
      width: 80%;
      height: 55%;
      padding-left:50px;
  }

  .titulo1{ /*TÍTULOS COM ESPAÇAMENTO ALTERADO*/
      flex-wrap: wrap;
      width: 120%;
      height: 40%;
      color: white;
      font-size: 47px;
      font-weight: bold;
      padding-bottom: 70px;
  }

  .subtitulo1{
      flex-wrap: wrap;
      width: 100%;
      height: 27%;
      color: white;
      font-size: 22px;
      font-weight: bold;
      line-height: 1.5;
      padding-bottom: 95px;
  }

  /*botão "quero ser nubank"*/
  .botao{
      box-shadow: 0 0 0.1em rgb(0, 0, 0);
      background-color: rgb(138, 5, 190);
      display: flex;
      text-decoration: none;
      font-size: 17px;
      color: white;
      font-weight: bold;
      border-radius:6.25rem;
      height: 50px;
      width: 180px;
      display: flex;
      align-items: center;
      justify-content: center;
  }

  /*bloco para imagem sobreposta do celular*/
  .cont-direito{ /*IMAGEM DO CELULAR COM AS DIMENSÕES ALTERADAS*/
      height: 80%;
      width: 100%;
      display: flex;
      flex-wrap: nowrap;
      position: absolute;
  }

  .cont-direito img{
      height: 55%;
      padding-top:45%;
      padding-left: 46%;
  }

  .cont-direito-mobile{ /*IMAGEM DO CELULAR ADAPTADA PARA A VERSÃO MOBILE: O CELULAR FICA EM CIMA DO CARTÃO E NÃO AO LADO - NÃO EXIBIDO*/
      display: none;
  }


