  .bt_elementos{ /*BOTÃO DE MENU PARA A VERSÃO MOBILE*/
      display: flex;
      width: 300px;
      margin-left: 290px;
      margin-bottom: 6px;
  }

  .bt_elementos a{ /*ESPECIFICAÇÕES DE FONTE E COR DO TEXTO DO MENU. TAMBÉM FOI FEITO UM CÍRCULO PARA O FORMATO DO BOTÃO*/
      font-weight: lighter;
      text-decoration: none;
      color: #494949;
      font-size: 33px;
      display: -webkit-flex; /*TRABALHO COM O FLEXBOX PARA CENTRALIZAR O TEXTO DENTRO DO CÍRCULO, SEM ELE, FICA TORTO*/
      display: -ms-flexbox;
      display: flex;
      -webkit-align-items: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      border-radius: 40px; /*FORMANDO O CÍRCULO EM VOLTA DO MENU*/
      height: 35px;
      width: 35px;
      padding: 5px;
      position: relative;
      -webkit-transition:background 0.6s ease-in-out; /*EFEITO FADE DO MENU*/
      -moz-transition:background 0.6 ease-in-out;
      -o-transition:background 0.6s ease-in-out;
      -ms-transition:background 0.6s ease-in-out;
      justify-content: center;
  }

  .bt_elementos a:hover{ /*EFEITO HOVER DO BOTÃO DE MENU*/
      background-color: #EFD8FA;
      display: -webkit-flex; /*TRABALHO COM O FLEXBOX PARA CENTRALIZAR O TEXTO DENTRO DO CÍRCULO, SEM ELE, FICA TORTO*/
      display: -ms-flexbox;
      display: flex;
      -webkit-align-items: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      border-radius: 40px; /*FORMANDO O CÍRCULO EM VOLTA DO MENU*/
      height: 35px;
      width: 35px;
      padding: 5px;
      position: relative;
      -webkit-transition:background 0.8s ease-in-out; /*EFEITO FADE DO MENU*/
      -moz-transition:background 0.8 ease-in-out;
      -o-transition:background 0.8s ease-in-out;
      -ms-transition:background 0.8s ease-in-out;
      justify-content: center;
  }

